import { ReactComponent as TabsIcon } from '@/images/tabs.svg';
import { MenuItem } from '@/components/tool';
import { useEditor, useEditorActions } from 'topwrite';

export default function Tabs() {
    const editor = useEditor();
    const { insertContainerDirective } = useEditorActions();

    const addTabs = () => {
        insertContainerDirective(editor, {
            name: 'tabs',
            children: [
                {
                    name: 'tab',
                    text: '标签一'
                },
                {
                    name: 'tab',
                    text: '标签二'
                }
            ]
        });
    };

    return <MenuItem
        icon={<TabsIcon />}
        title={'选项卡'}
        description={'可切换显示内容'}
        onMouseDown={addTabs}
    />;
}
