import { Modal, styled, useEditor, useEditorActions } from 'topwrite';
import { MenuItem } from '@/components/tool';
import { ReactComponent as Icon } from '../../images/link.svg';

export default function Link() {
    const editor = useEditor();
    const { insertLeafDirective } = useEditorActions();

    const addLink = async () => {
        const result = await Modal.prompt<{ url: string, title: string }>({
            title: '插入链接',
            schema: {
                type: 'object',
                properties: {
                    url: {
                        type: 'string',
                        title: '链接'
                    },
                    title: {
                        type: 'string',
                        title: '标题'
                    }
                },
                required: ['url']
            }
        });

        if (result) {
            insertLeafDirective(editor, { name: 'link', attributes: { url: result.url }, text: result.title });
        }

    };

    return <MenuItem
        icon={<StyledIcon />}
        title={'链接卡片'}
        description={'插入整行链接'}
        onMouseDown={addLink}
    />;
}

const StyledIcon = styled(Icon)`
  padding: 4px;
`;
