import { ReactComponent as CatalogIcon } from '@/images/catalog.svg';
import { MenuItem } from '@/components/tool';
import { useEditor, useEditorActions } from 'topwrite';

export default function Catalog() {
    const editor = useEditor();
    const { insertLeafDirective } = useEditorActions();

    const addCatalog = () => {
        insertLeafDirective(editor, {
            name: 'catalog',
        });
    };

    return <MenuItem
        icon={<CatalogIcon />}
        title={'目录'}
        description={'插入目录卡片'}
        onMouseDown={addCatalog}
    />;
}
