import { ReactComponent as CardIcon } from '@/images/card.svg';
import { MenuItem } from '@/components/tool';
import { GetComponentProps, Modal, useAsset, useEditor, useEditorActions } from 'topwrite';
import parseCard, { Card as CardType } from '../../utils/parse-card';
import { useMemo } from 'react';

type Options = Parameters<typeof Modal.prompt<CardType & { type: string }>>[0]

const useOptions = (data?: { value: string, parameter: string }): Options => {
    const { upload, getUrl } = useAsset();

    const options: Options = {
        title: '插入卡片',
        schema: {
            type: 'object',
            properties: {
                type: {
                    type: 'string',
                    title: '视图模式',
                    enum: ['normal', 'left'],
                    default: 'normal'
                },
                title: {
                    type: 'string',
                    title: '标题'
                },
                description: {
                    type: 'string',
                    title: '描述'
                },
                url: {
                    type: 'string',
                    title: '链接'
                },
                image: {
                    type: 'string',
                    title: '图片'
                }
            },
            required: ['title']
        },
        uiSchema: {
            type: {
                'ui:widget': 'radio',
                'ui:options': {
                    enumNames: ['上下结构', '左右结构']
                }
            },
            image: {
                'ui:widget': 'upload',
                'ui:options': {
                    onUpload(file: File): Promise<string> {
                        return upload(file);
                    },
                    renderIcon(url: string) {
                        return <img src={getUrl(url)} width={24} height={24} />;
                    }
                }
            }
        }
    };

    if (data) {
        const { value, parameter } = data;
        const card = useMemo(() => {
            return parseCard(value);
        }, [value]);

        options.formData = {
            ...card,
            type: parameter || 'normal'
        };
    }

    return options;
};

export function CardEdit({ Component, props }: GetComponentProps<'x-element:tool:edit'>) {

    const editor = useEditor();
    const { element } = props;

    const { value, parameter } = element;

    const options = useOptions({ value, parameter });

    const onEdit = async () => {
        const { setNodes, findPath } = useEditorActions();
        const result = await Modal.prompt(options);

        if (result) {
            const value = `${result.title}\n${result.description || ''}\n${result.url || ''}\n${result.image || ''}`;

            const location = findPath(editor, element);
            setNodes(editor, { value, parameter: result.type === 'left' ? 'left' : undefined, }, { at: location });
        }
    };

    return <Component {...props} onMouseDown={onEdit} />;
}

export default function Card() {

    const editor = useEditor();
    const { insertXBlock } = useEditorActions();
    const options = useOptions();

    const addCard = async () => {
        const result = await Modal.prompt(options);

        if (result) {
            const value = `${result.title}\n${result.description || ''}\n${result.url || ''}\n${result.image || ''}`;

            insertXBlock(editor, {
                name: 'card',
                parameter: result.type === 'left' ? 'left' : undefined,
                value
            });
        }
    };

    return <MenuItem
        icon={<CardIcon />}
        title={'图文卡片'}
        description={'使用卡片视图'}
        onMouseDown={addCard}
    />;
}
