import { ReactComponent as ColumnsIcon } from '@/images/columns.svg';
import { MenuItem } from '@/components/tool';
import { useEditor, useEditorActions } from 'topwrite';

export default function Columns() {
    const editor = useEditor();
    const { insertContainerDirective } = useEditorActions();

    const addColumns = () => {
        insertContainerDirective(editor, {
            name: 'columns',
            children: [
                {
                    name: 'column'
                },
                {
                    name: 'column'
                }
            ]
        });
    };

    return <MenuItem
        icon={<ColumnsIcon />}
        title={'分栏'}
        description={'使用分栏组织页面'}
        onMouseDown={addColumns}
    />;
}
