import { GetComponentProps, styled, useOverlayState } from 'topwrite';
import { ReactComponent as Icon } from '../images/icon.svg';
import { createContext, ReactElement, useContext, useRef } from 'react';
import { Overlay, Popover } from 'react-bootstrap';
import Collapse from '@/components/tool/collapse';
import Tabs from '@/components/tool/tabs';
import Columns from '@/components/tool/columns';
import Card from '@/components/tool/card';
import Link from '@/components/tool/link';
import Catalog from '@/components/tool/catalog';

const ToolContext = createContext({
    hide: () => {

    }
});

export default function Tool({ Component }: GetComponentProps<'editor:tool'>) {

    const { visible, show, hide, state } = useOverlayState();
    const target = useRef(null);

    return <ToolContext.Provider value={{ hide }}>
        <Component
            ref={target}
            active={visible}
            handler={show}
            title={'UI组件'}>
            <Icon />
        </Component>
        <Overlay
            target={target.current}
            placement='bottom'
            rootClose={true}
            rootCloseEvent='mousedown'
            {...state}
        >
            <Popover>
                <Popover.Body as={Container}>
                    <Collapse />
                    <Tabs />
                    <Columns />
                    <Card />
                    <Link />
                    <Catalog />
                </Popover.Body>
            </Popover>
        </Overlay>
    </ToolContext.Provider>;
}

interface MenuItemProps {
    icon: ReactElement;
    title: string;
    description: string;
    onMouseDown: () => void;
}

export const MenuItem = ({ icon, title, description, onMouseDown }: MenuItemProps) => {

    const { hide } = useContext(ToolContext);

    return <Item onMouseDown={(e) => {
        e.preventDefault();
        hide();
        onMouseDown();
    }}>
        <ItemIcon>
            {icon}
        </ItemIcon>
        <ItemContent>
            <h4>{title}</h4>
            <p>{description}</p>
        </ItemContent>
    </Item>;
};


const ItemIcon = styled.div`
  margin-right: 8px;
  border-radius: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e0e0e0;
  width: 36px;
  height: 36px;
`;

const ItemContent = styled.div`
  h4 {
    font-size: 14px;
    line-height: 18px;
    color: #262626;
    margin-bottom: 0;
  }

  p {
    font-size: 12px;
    line-height: 18px;
    color: #8A8F8D;
    margin-bottom: 0;
  }
`;

const Item = styled.div`
  display: flex;
  width: 200px;
  align-items: center;
  padding: 6px;
  cursor: pointer;

  &:hover {
    background-color: #EFF0F0;
    border-radius: 5px;
  }
`;

const Container = styled.div`
  padding: 10px;
`;
