import { ReactComponent as CollapseIcon } from '@/images/collapse.svg';
import { MenuItem } from '@/components/tool';
import { useEditor, useEditorActions } from 'topwrite';

export default function Collapse() {
    const editor = useEditor();
    const { insertContainerDirective } = useEditorActions();

    const addCollapse = () => {
        insertContainerDirective(editor, { name: 'collapse' });
    };

    return <MenuItem
        icon={<CollapseIcon />}
        title={'折叠块'}
        description={'可折叠内容'}
        onMouseDown={addCollapse}
    />;
}
